<template>
  <div class="hs-container">
    <hs-scroll
      ref="tableView"
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getDetails">
      <ul class="bg" :style="{'background-image': `url('static/image/personal-center/person_bg.png')`}">
        <div class="header">
          <div class="logo" :style="{'background-image': `url('static/image/personal-center/head.png')`}"></div>
          <div class="des" v-if="details.UserInfo">
            <h2>{{details.UserInfo.Name}}</h2>
            <div class="item">
              <p>{{details.UserInfo.CompanyName}}</p>
            </div>
          </div>
        </div>
        <div class="section" style="margin-top: 0px">
          <div class="item" @click="$router.push({path: '/message/examine', query: {QuerySource: 0}})" v-if="details.EnableVetting">
            <div class="logo" style="background-image: url('static/image/management/pendingApproval.png')"></div>
            <p>{{$t('base.MyApprovals')}}</p>
            <div class="badge" v-if="details && details.MeProcessVettingCountInfo">{{details.MeProcessVettingCountInfo.UnHandleVettingCount}}</div>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="item" @click="$router.push({path: '/message/examine', query: {QuerySource: 1}})">
            <div class="logo" style="background-image: url('static/image/management/launchApproval.png')"></div>
            <p>{{$t('base.MyInitiations')}}</p>
            <div class="badge" v-if="details && details.MeStartVettingCountInfo">{{details.MeStartVettingCountInfo.UnHandleVettingCount}}</div>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
        </div>
        <div class="section" v-if="details.EnableTravelerManage || details.EnableDefaultContactManage">
          <div class="item" v-if="details.EnableTravelerManage" @click="$router.push({path: '/personal/passengers'})">
            <div class="logo" style="background-image: url('static/image/management/traveller.png')"></div>
            <p style="width: calc(100% - 65px)">{{$t('base.FrequentTravelerManagement')}}</p>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="item" v-if="details.EnableDefaultContactManage" @click="$router.push({path: '/personal/frequentContacts'})">
            <div class="logo" style="background-image: url('static/image/management/Contacts.png')"></div>
            <p style="width: calc(100% - 65px)">{{$t('base.FrequentContactsManagement')}}</p>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
        </div>
      </ul>
    </hs-scroll>
    <c-tabbar></c-tabbar>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import CTabbar from 'components/c-tabbar/c-tabbar'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {kpiUserGetUserManageInfo} from 'api'
export default {
  created() {
    this._getDetails()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {}
    }
  },
  methods: {
    _getDetails() {
      kpiUserGetUserManageInfo({}).then((res) => {
        console.log('管理: ', res)
        this.details = res.ResultData
      }).catch(() => {
        this.$refs.tableView.forceUpdate()
      })
    },
    _callPhone(mobile) {
      window.location.href = `tel:${mobile}`
    }
  },
  components: {
    CHeader,
    CTabbar,
    HsScroll,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 50px)
    overflow hidden
    .bg
      background-size 100% auto
      background-position center top
      background-repeat no-repeat
    .header
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 20px 15px
      >.logo
        width 50px
        height 50px
        background-size 100% 100%
        box-shadow 0 0 5px $color_black_text_1
        border-radius 25px
      >.des
        @extend .flex-column
        @extend .flex-center
        @extend .flex-top
        width calc(100% - 50px - 15px)
        min-height 60px
        >h2
          font-size 1.2rem
          color $color_white
          font-weight bold
          margin-bottom 10px
        >.item
          @extend .flex-row
          @extend .flex-start
        p
          color $color_white
          font-size 0.9rem
          line-height 1.2em
        p[type="EN"]
          font-size 0.8rem
    .section
      width calc(100% - 20px)
      margin-left 10px
      background-color $color_white
      border-radius 5px
      border 1px solid $color_gray_layer
      padding 5px 10px
      margin-top 10px
      >.item
        @extend .flex-row
        @extend .flex-start
        width 100%
        height 50px
        border-bottom 1px solid $color_gray_layer
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          margin-right 10px
        >p
          font-size 1rem
          width calc(100% - 80px)
          color $color_black_text_0
        >.badge
          @extend .flex-center
          width 20px
          height 20px
          border-radius 10px
          background-color $color_pink_background_1
          color $color_theme
        >.arrow
          width 20px
          height 20px
          background-size 100% 100%
          margin-left 5px
        &:last-child
          border-bottom-width 0px
    // .header
    //   @extend .flex-row
    //   @extend .flex-between
    //   width 100%
    //   background-color $color_white
    //   padding 20px 15px
    //   >.logo
    //     width 46px
    //     height 46px
    //     background-size 100% 100%
    //   >.des
    //     width calc(100% - 46px - 20px)
    //     >p
    //       font-size 1rem
    //       &:nth-child(1)
    //         font-weight bold
    //         line-height 1.5em
    //       &:nth-child(2)
    //       &:nth-child(3)
    //         line-height 1.3em
    //         font-size 0.8rem
    //         color $color_gray_text
    // .approval-entry
    //   @extend .flex-row
    //   @extend .flex-between
    //   width 100%
    //   padding 0 15px 20px 15px
    //   >.item
    //     @extend .flex-row
    //     @extend .flex-start
    //     width calc(50% - 10px)
    //     height 44px
    //     box-shadow 0 0 10px $color_gray_layer
    //     >.logo
    //       width 20px
    //       height 20px
    //       background-size 100% 100%
    //       margin 0 5px 0 10px
    //     >p
    //       font-size 0.9rem
    //       width calc(100% - 75px)
    //     >.badge
    //       @extend .flex-center
    //       width 20px
    //       height 20px
    //       border-radius 10px
    //       background-color $color_pink_background_1
    //       color $color_theme
    //     >.arrow
    //       width 20px
    //       height 20px
    //       background-size 100% 100%
    //       margin 0 5px
    .custom-cell-logo
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 10px
      >p
        width calc(100% - 30px)
</style>



